.stars {
  display: flex;
  flex-direction: row;

  &__star {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 16px;
    height: 16px;
    margin-right: 4px;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }
}

@for $i from 1 through 5 {
  .stars--#{$i} {
    .stars__star:nth-child(-n + #{$i}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
