.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  display: block;
}

.nav {
  height: 60px;
}

.nav__list {
  height: 60px;
  margin: 0;
  padding: 0;
  line-height: 60px;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: block;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  max-width: 1024px;
  margin-inline: auto;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 1fr);
  justify-items: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 10px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
}

.card__link {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.rating__desc {
  line-height: 14px;
}

body {
  margin: 0;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 400;
}
